* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* :before, * :after {
  box-sizing: border-box;
}

#html {
  word-break: break-all;
}

#div1 {
  position: fixed;
  top: 20px;
  right: 50%;
  transform: translateX(50%);
}

#div1:before, #div1:after {
  content: "";
  display: block;
  position: absolute;
}

@media (max-width: 500px) {
  #html {
    height: 50vh;
    overflow: auto;
  }

  #div1Wrapper {
    height: 50vh;
  }

  #div1 {
    position: relative;
    top: 0;
    right: 0;
  }
}

